body {
  margin: 0;
}


#box{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  transition: all 1s ease-in;
  margin: 10vh;
  font-family: monospace;
}
#author{
  padding: 5px;
  font-size: large;
}
#box button{
  border-radius: 10px;
  padding: 10px;
  border: 0;
  display: block;
  margin: 0 auto;
  background-color: #686de0;
  color: white;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  transition: .5s ease;
}
.item:hover{
  transform: scale(1.1);
}
#loading{
  position: fixed;
  height: 50px;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
}
#box button:hover{
  background-color: #30336b;
  transform: scale(1.1);
}
.item{
  margin: 10px;
  padding: 20px;
  box-shadow: 0 0 15px grey;
  transition: .3s ease-in-out;
}

#box img{
  min-width: 100%;
  height: 200px;
}
